import React from "react";
import ReactDOM from "react-dom";
import "../css/calculator.css";

class Calculator extends React.Component {
  keys = {
    row1: [
      { value: "C", callFunction: "display" },
      { value: "1", callFunction: "display" },
      { value: "2", callFunction: "display" },
      { value: "3", callFunction: "display" },
    ],
    row2: [
      { value: "/", callFunction: "display" },
      { value: "4", callFunction: "display" },
      { value: "5", callFunction: "display" },
      { value: "6", callFunction: "display" },
    ],
    row3: [
      { value: "-", callFunction: "display" },
      { value: "7", callFunction: "display" },
      { value: "8", callFunction: "display" },
      { value: "9", callFunction: "display" },
    ],
    row4: [
      { value: "+", callFunction: "display" },
      { value: ".", callFunction: "display" },
      { value: "0", callFunction: "display" },
      { value: "=", callFunction: "calculate" },
    ],
    row5: [{ value: "*", callFunction: "display" }],
  };
  constructor() {
    super();
  }

  display(cellText) {
    // alert(cellText);
    if (cellText !== "C") {
      var result = document.getElementById("result");
      result.value += cellText;
    } else if (cellText !== "=") {
      document.getElementById("result").value = "";
    }
  }

  calculate(cellText) {
    try {
      var result = eval(document.getElementById("result").value);
      document.getElementById("result").value = result;
    } catch {
      alert("Please type the proper addition format");
    }
  }

  generateCell(keyObj) {
    return (
      <td>
        <input
          className="button"
          type="button"
          value={keyObj.value}
          onClick={() => this[keyObj.callFunction](keyObj.value)}
          key={keyObj.value}
        />
      </td>
    );
  }

  generateRow(data) {
    let eachCell = data.map((cell) => this.generateCell(cell));
    return <tr key={eachCell[0].value}>{eachCell}</tr>;
  }

  generateBody() {
    // let data;
    // for(let i=0;i<=this.keys.length;i+4){
    //     data += (this.keys.slice(i,i+4))
    // },
    let objectKeys = Object.keys(this.keys);
    console.log(objectKeys);
    let data = objectKeys.map((eachKey) =>
      this.generateRow(this.keys[eachKey])
    );
    return data;
    // let initial = 0;
    // let end = 4;
    // let data = this.keys.forEach(eachObj =>{
    //     if(this.keys.indexOf(eachObj)%4==0){
    //         initial += 4;
    //         end+=4;
    //         return this.keys.slice(initial,end)
    //     }
    // })

    // console.log(data);
    // return this.generateRow(data);
  }

  render() {
    let displayText = this.generateBody();
    return (
      <div className="calculatorPage">
        <input className="display-box" type="text" id="result" disabled />

        <table className="calculator">
          <tbody>.'//;;

            {/* <tr>
            {" "}
            
            </td>
            <td>
              {" "}
              <input className="button" type="button" value="C" />{" "}
            </td>
          </tr> */}

            {displayText}
            {/* <tr>
                    <td> <input className="button" type="button" value="1" onClick="display('1')" /> </td>
                    <td> <input className="button" type="button" value="2" onClick="display('2')" /> </td>
                    <td> <input className="button" type="button" value="3" onClick="display('3')" /> </td>
                    <td> <input className="button" type="button" value="/" onClick="display('/')" /> </td>
                </tr>
                <tr>
                    <td> <input className="button" type="button" value="4" onClick="display('4')" /> </td>
                    <td> <input className="button" type="button" value="5" onClick="display('5')" /> </td>
                    <td> <input className="button" type="button" value="6" onClick="display('6')" /> </td>
                    <td> <input className="button" type="button" value="-" onClick="display('-')" /> </td>
                </tr>
                <tr>
                    <td> <input className="button" type="button" value="7" onClick="display('7')" /> </td>
                    <td> <input className="button" type="button" value="8" onClick="display('8')" /> </td>
                    <td> <input className="button" type="button" value="9" onClick="display('9')" /> </td>
                    <td> <input className="button" type="button" value="+" onClick="display('+')" /> </td>
                </tr>

                <tr>
                    <td> <input className="button" type="button" value="." onClick="display('.')" /> </td>
                    <td> <input className="button" type="button" value="0" onClick="display('0')" /> </td>
                    <td> <input className="button" type="button" value="=" onClick="calculate()" /> </td>
                    <td> <input className="button" type="button" value="*" onClick="display('*')" /> </td>
                </tr> */}
          </tbody>
        </table>
      </div>
    );
  }
}
export default Calculator;
