import React from "react";
import { Link } from "react-router-dom";
import "../css/navbar.css";

class Navbar extends React.Component {
  constructor() {
    super();
  }
  render() {
    return (
      <nav>
        <ul>
          <li>
            <Link to={"/login"}>
              <i className="far fa-address-card"></i>Login
            </Link>
          </li>
          <li>
            <Link to={"/signup"}>
              <i className="fas fa-user-plus"></i>Signup
            </Link>
          </li>

          <li>
            <Link to={"/Objects"}>
              <i className="far fa-address-book"></i>Comments
            </Link>
          </li>
          <li>
            <Link to={"/clock"}>
              <i className="fas fa-clock"></i>Clock
            </Link>
          </li>
          {/* <li>
            <a href="/signup">Signup</a>
          </li> */}
          <li>
            <a href="/calculator">
              <i className="fas fa-calculator"></i>Calculator
            </a>
          </li>
          <li>
            <Link to={"/chart"}>
              <i className="fas fa-clock"></i>Chart
            </Link>
          </li>
          <li>
            <Link to={"/profilecard"}>
              <i className="fas fa-clock"></i>Profile Card
            </Link>
          </li>

          <li>{/* <Link to={"/Example"}>Example</Link> */}</li>
        </ul>
      </nav>
    );
  }
}

export default Navbar;
